}
.video{
  width:60%;
  position:fixed;
  bottom:20%;
  height:36%;
  left:20%;
  border-radius: 50%;
  overflow: hidden;
   box-shadow:
    0 0 30px 10px #ffeefc;
}
.text{
  display: none;
  width:40%;
  position:fixed;
  bottom:6%;
  height:42%;
  right:7%;
}
.frame{
  display: none;
  position:absolute;
  box-sizing: border-box;
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    border:1px solid white;
    border-radius: 50%;
     box-shadow:
    0 0 30px 10px #ffeefc;
    width:100%;
    height:100%;
}
